html,
body {
  margin: 0;
  padding: 0;
}

body {
  font: 12px "Noto Sans", Helvetica, Arial, sans-serif;
  margin: 0 auto;
  background: #f5f5f5;
  color: #19254e;
  min-width: 230px;
  max-width: 550px;
  font-weight: 100;
}

.time-controller {
  position: relative;
  border-radius: 2px;
}  

.headline {
  width: 100%;
  font-size: 50px;
  font-weight: 100;
  text-align: center;
  color: rgba(44, 156, 248, 1);
  text-rendering: optimizeLegibility;
  margin: 20px auto;
}

.indicator {
  background: #fff;
  width: 100%;
  font-size: 65px;
  font-weight: 100;
  text-align: center;
  color: rgba(11, 8, 8, 0.15);
  text-rendering: optimizeLegibility;
  box-shadow: 0 1px 0px 0 rgb(0 0 0 / 10%), 0 25px 50px 0 rgb(0 0 0 / 10%);
  margin: 0 auto;
  border-radius: 6px 6px 0px 0px;
}

.main {
  padding-top: 40px;
  padding-bottom: 20px;
  border: none;
  box-shadow: inset 0 -2px 1px rgb(0 0 0 / 10%);
  background: #fff;
  border-radius: 0px 0px 6px 6px;
}
.container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0;
  padding: 0;
  position: relative;
  font-size: 24px;
}

.button {
  border-width: 1px;
  cursor: pointer;
  justify-content: center;
  padding-bottom: calc(0.5em - 1px);
  padding-left: 1em;
  padding-right: 1em;
  padding-top: calc(0.5em - 1px);
  text-align: center;
  white-space: nowrap;
  border-radius: 5px;
  margin: auto;

  &:hover {
    background-color: lightgray;
  }
}   

.color {
  background-color: #545151;
  border-color: transparent;
  color: #fff
}